h1 {
  font-family: 'Rubik Gemstones', cursive;
}

.banner {
  width: 100%;
  background-color: navy;
  color: white;
  display: flex;
  padding: 1.5rem 0rem;
  position: fixed;
  bottom: 0;
}

.banner .title {
  flex: 1;
}

.banner .options a {
  margin: 0 .4rem;
  cursor: pointer;
  font-weight: bold;
}

.banner .options a:active {
  background-color: white;
} 

img {
  width: 100%;
}